<template>
    <div id="detailContainer" class="slide-enter-active">        
        <Header title="影片详情" >
             <i class="iconfont iconzuojiantou1" @touchstart="handleToBack"></i>
        </Header>
        <!-- <Loading v-if="isLoading" /> -->
        <div  id="content" class="contentDetail">
            <!-- <div class="detail_list">
                <div class="detail_list_bg"></div>
                <div class="detail_list_filter"></div>
                <div class="detail_list_content">
                    <div class="detail_list_img">
                        <img src="/images/movie_1.jpg" alt="">
                    </div>
                    <div class="detail_list_info">
                        <h2>片名</h2>
                        <p>英文片名</p>
                        <p>9.2</p>
                        <p>剧情</p>
                        <p>中国大陆 / 108分钟</p>
                        <p>2018-11-16大陆上映</p>
                    </div>
                </div>
            </div>
            <div class="detail_intro">
                <p>影片简介</p>
            </div>
            <div class="detail_player swiper-container">
                <ul class="swiper-wrapper">
                    <li class="swiper-slide">
                        <div>
                            <img src="/images/movie_1.jpg" alt="">
                        </div>
                        <p></p>
                        <p></p>
                    </li>
                </ul>
            </div> -->
            <div class="noData" v-if="show">
                {{ noData }}
            </div>
            <div class="detail_list">
                <div class="detail_list_bg" :style="{ 'background-image' : 'url('+ detailMovie.img.replace(/w\.h/,'148.208') +')' }"></div>
                <div class="detail_list_filter"></div>
                <div class="detail_list_content">
                    <div class="detail_list_img">
                        <img :src="detailMovie.img | setWH('148.208') " alt=""> 
                    </div>
                    <div class="detail_list_info">
                        <h2>{{ detailMovie.nm }}</h2>
                        <p>{{ detailMovie.enm }}</p>
                        <p>{{ detailMovie.scoreLabel }}</p>
                        <p>{{ detailMovie.cat }}</p>
                        <p>{{ detailMovie.src }} / {{ detailMovie.dur }}</p>
                        <p>{{ detailMovie.pubDesc }}</p>
                    </div>
                </div>
            </div>
            <div class="detail_intro">
                <p>{{ detailMovie.dra }}</p>
            </div>
            <div class="detail_player swiper-container" ref="detail_player">
                <ul class="swiper-wrapper">
                    <li v-for="(item,index) in detailMovie.photos" :key="index" class="swiper-slide">
                        <div>
                            <img :src="item" alt="">
                        </div>
                        <p>演员A</p>
                        <p>（饰演：主角A）</p>
                    </li>
                </ul>
            </div> 
        </div>
    </div>
    
</template>

<script >

import Header from '@/components/Header';

export default {
    name : 'Detail',
    data() {
        return {
            detailMovie : {},
            noData : '',
            show : false,
            isLoading : true
        }

    },
    components : {
        Header
    },
    props : ['movieId'],
    methods : {
        handleToBack(){
            this.$router.back();
        }
    },
    mounted(){
        console.log( this.movieId );
        this.axios.get('/api/detailmovie?movieId='+ this.movieId ).then((res)=>{
            var msg = res.data.msg;
            console.log(msg);
            if( msg === 'ok' ){               
                if(res.data.data && res.data.data.detailMovie){  
                    setTimeout(()=>{
                        this.isLoading = false;
                    },2000);                                      
                    this.detailMovie = res.data.data.detailMovie;  
                    this.$nextTick(()=>{
                        new Swiper(this.$refs.detail_player , {
                            slidesPerView : 'auto',
                            freeMode : true,
                            freeModeSticky : true
                        })

                    })
                   
                }else{
                    this.show = true;
                    this.noData = '暂无数据，测试数据：'; 
                    setTimeout(()=>{
                        this.isLoading = false;
                    },2000); 
                    this.detailMovie = {
                        nm : '测试片名',
                        enm : '英文片名',
                        sc : '9.2',
                        cat : '剧情',
                        src : '中国大陆',
                        dur : '108分钟',
                        pubDesc : '2018-11-16大陆上映',
                        img : 'http://p0.meituan.net/w.h/movie/c6d7c3d84d54abc910f85468db9985362428931.jpg',
                        dra : '剧情介绍：...',
                        photos : [
                            "/images/movie_1.jpg",
                            "/images/movie_2.jpg",
                             "/images/movie_1.jpg",
                            "/images/movie_2.jpg",
                             "/images/movie_1.jpg",
                            "/images/movie_2.jpg"
                        ]

                    }
                    
                    this.$nextTick(()=>{
                        new Swiper(this.$refs.detail_player , {
                            slidesPerView : 'auto',
                            freeMode : true,
                            freeModeSticky : true
                        })

                    })
                     
                   
                    

                }
                

            }

        })
    }

}
</script>

<style scoped>
#detailContainer{ position: absolute; left:0; top:0; z-index: 100; width:100%; min-height: 100%;background: #fff;}            
#detailContainer.slide-enter-active{ animation: .3s slideMove;}
@keyframes slideMove{
    0%{ transform : translateX(100%); }
    100%{ transform: translateX(0); }
}
#content.contentDetail{ display: block; margin-bottom: 0;}
#content .detail_list{ height: 200px; width:100%; position: relative; overflow: hidden;}
.detail_list .detail_list_bg{width:100%; height: 100%; background: 0 40% ;filter: blur(20px); background-size:cover; position: absolute; left: 0; top: 0;}
.detail_list .detail_list_filter{ width:100%; height: 100%;position: absolute;background-color: #40454d;opacity: .55;position: absolute;left: 0; top: 0; z-index: 1;}
.detail_list .detail_list_content{ display: flex; width:100%; height: 100%;position: absolute; left:0; top:0; z-index: 2;}
.detail_list .detail_list_img{ width:108px; height: 150px; border: 1px solid #f0f2f3; margin:20px;}
.detail_list .detail_list_img img{ width:100%; height: 100%;}
.detail_list .detail_list_info{ margin-top: 20px;}
.detail_list .detail_list_info h2{ font-size: 20px; color:white; font-weight: normal; line-height: 40px;}
.detail_list .detail_list_info p{ color: white; line-height: 20px; font-size: 14px; color:#ccc;}
#detailContainer .noData{width:100%; text-align: center;}

#content .detail_intro{ padding: 10px;}
#content .detail_player{ margin:20px;}
.detail_player .swiper-slide{ width:70px; margin-right: 20px; text-align: center; font-size: 14px;}
.detail_player .swiper-slide img{ width:100%; margin-bottom: 5px;}
.detail_player .swiper-slide p:nth-of-type(2){ color: #999;}
</style>
